<template>
  <el-collapse v-model="activeName" accordion>
    <el-collapse-item name="1">
      <template slot="title">
         Consistency<i class="header-icon el-icon-info"></i>
      </template>
      <div id="conatinContent">
          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="" prop="name">
              <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="region">
              <el-select v-model="ruleForm.region" placeholder="">
                <el-option label="" value="shanghai"></el-option>
                <el-option label="" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" required>
              <el-col :span="11">
                <el-form-item prop="date1">
                  <el-date-picker type="date" placeholder="" v-model="ruleForm.date1" style="width: 100%;"></el-date-picker>
                </el-form-item>
              </el-col>
              <el-col class="line" :span="2">-</el-col>
              <el-col :span="11">
                <el-form-item prop="date2">
                  <el-time-picker placeholder="" v-model="ruleForm.date2" style="width: 100%;"></el-time-picker>
                </el-form-item>
              </el-col>
            </el-form-item>
            <el-form-item label="" prop="delivery">
              <el-switch v-model="ruleForm.delivery"></el-switch>
            </el-form-item>
            <el-form-item label="" prop="type">
              <el-checkbox-group v-model="ruleForm.type">
                <el-checkbox label="/" name="type"></el-checkbox>
                <el-checkbox label="" name="type"></el-checkbox>
                <el-checkbox label="" name="type"></el-checkbox>
                <el-checkbox label="" name="type"></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
            <el-form-item label="" prop="resource">
              <el-radio-group v-model="ruleForm.resource">
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="" prop="desc">
              <el-input type="textarea" v-model="ruleForm.desc"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
              <el-button @click="resetForm('ruleForm')"></el-button>
            </el-form-item>
          </el-form>
      </div>
    </el-collapse-item>
    <el-collapse-item title=" Feedback" name="2">
      <div></div>
      <div></div>
    </el-collapse-item>
    <el-collapse-item title=" Efficiency" name="3">
      <div></div>
      <div></div>
      <div></div>
    </el-collapse-item>
    <el-collapse-item title=" Controllability" name="4">
      <div></div>
      <div></div>
    </el-collapse-item>
  </el-collapse>
</template>

<script>
export default {
  data() {
    return {
      activeName: '1',
      tableData: [{
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-04',
          name: '',
          address: ' 1517 '
        }, {
          date: '2016-05-01',
          name: '',
          address: ' 1519 '
        }, {
          date: '2016-05-03',
          name: '',
          address: ' 1516 '
        }],
      ruleForm: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        region: [
          { required: true, message: '', trigger: 'change' }
        ],
        date1: [
          { type: 'date', required: true, message: '', trigger: 'change' }
        ],
        date2: [
          { type: 'date', required: true, message: '', trigger: 'change' }
        ],
        type: [
          { type: 'array', required: true, message: '', trigger: 'change' }
        ],
        resource: [
          { required: true, message: '', trigger: 'change' }
        ],
        desc: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }  
    };
  },
  methods: {
    handleChange(val) {
      console.log(val);
    },
    handleEdit(index, row) {
      console.log(index, row);
    },
    handleDelete(index, row) {
      console.log(index, row);
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!');
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  created() {
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
h1 {
  font-size: 30px;
  color: #333;
  text-align: center;
  margin: 0 auto;
  padding-bottom: 5px;
  border-bottom: 2px solid #409eff;
  width: 300px;
}

.el-collapse-item .el-collapse-item__header {
  background-color: #a67a67;
  border-radius: 4px;
}

#conatinContent {
  margin-top: 10px;
}
</style>



